.test-button{
    position: relative;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid white;
    border-radius: 35px;
    width: 300px;
    min-height: 70px;
    font-size: 20px;
    font-weight: lighter;
    margin: 20px 0;
    color: white;
    font-family: inherit;
    padding: 10px 20px;
}

.test-button span{
    position: absolute;
    left: -4px;
    top: -4px;
    right: -4px;
    bottom: -4px;
    border: 1px solid rgba(255, 255, 255, 0);
    border-radius: 39px;
    margin-bottom: 0px;
}

.test-button:focus{
    outline: none;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, .4);
}

.test-button-true{
    border: 1px solid #1a361e;
    background-color: #1a361e;
    color: #4cd964;
}

.test-button-true span{
    border-color:  #1a361e;
    opacity: 1;
}

.test-button-false{
    border: 1px solid #3d1614;
    background-color: #3d1614;
    color: #ff3b30;
}

.test-button-false span{
    border-color: #3d1614;
    opacity: 1;
}

@media (max-width: 576px){

    .test-button{
        width: 250px;
        min-height: 50px;
        margin: 10px 0;
        font-size: 1rem;
    }

    .test-button span{
        left: -3px;
        top: -3px;
        right: -3px;
        bottom: -3px;
        border-radius: 38px;
    }
}