.footer{
    display: flex;
    flex-shrink: 0;
    background-color: #181818;
}

.footer>div{
    width: 100%;
    max-width: 1200px;
    min-height: 100px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}

.footer ul{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    max-width: 400px;
    flex-grow: 1;
}

.footer li a{
    color: white;
}

.footer li.logo-footer{
    display: none;
}

@media (max-width: 576px){
    .footer ul{
        width: 100%;
        margin: 0 25px;
    }

    .footer .site-name{
        display: none;
    }

    .footer li.logo-footer{
        display: block;
        font-size: 1.8rem;
        font-family: 'Rubik', sans-serif;
    }
}