.header{
    background-color: #181818;
    flex-shrink: 0;
}

.header>div{
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}

.header .logo{
    margin: 10px 25px;
    font-size: 3rem;
}

.header .logo a{
    color: white;
    font-family: 'Rubik', sans-serif;
}

.header nav ul{
    display: flex;
    justify-content: space-around;
}

.header li{
    min-width: 150px;
    text-align: center;
    user-select: none;
    color: white;
}

@media (max-width: 576px){
    .header>div{
        flex-direction: column-reverse;
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .header .logo{
        margin: 10px 25px;
        font-size: 1.8rem;
        width: 100%;
        padding: 15px;
        display: none;
    }
}