.interactive-window{
    position: fixed;
    top: 0;
    min-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #181818;
    align-items: center;
    justify-content: center;
    overflow: auto;
    z-index: 5;
}

.interactive-window .close-icon{
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.interactive-window p, h1, h2, h3, h4, h5, h6, span{
    color: white;
    font-weight: lighter;
    margin-bottom: 15px;
}