
.interactive-button{
    position: relative;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid rgb(83, 83, 83);
    border-radius: 35px;
    width: 140px;
    min-height: 70px;
    font-size: 20px;
    font-weight: lighter;
    margin: 20px 0;
    color: rgb(83, 83, 83);
    font-family: inherit;
    padding: 10px 0;
    user-select: none;
}

.interactive-button span{
    position: absolute;
    left: -4px;
    top: -4px;
    right: -4px;
    bottom: -4px;
    border: 1px solid  rgb(83, 83, 83);
    border-radius: 39px;
    margin-bottom: 0;
}

.interactive-button:focus{
    outline: none;
}

.is-active{
    border: 1px solid#ff9500;
    color: white;
    background-color:#ff9500;
}

.is-active span{
    border-color: #ff9500;
}

@media (max-width: 576px){

    .interactive-button{
        width: 120px;
        min-height: 50px;
        margin: 10px 0;
        font-size: 1rem;
    }

    .interactive-button span{
        left: -3px;
        top: -3px;
        right: -3px;
        bottom: -3px;
        border-radius: 38px;
    }
}