.main-button{
    position: relative;
    border: none;
    background-color: #ff9500;
    border-radius: 35px;
    width: 300px;
    min-height: 70px;
    font-size: 20px;
    font-weight: lighter;
    margin: 20px 0;
    color: white;
    font-family: inherit;
    user-select: none;
    padding: 5px 20px;
}

.main-button span{
    position: absolute;
    left: -4px;
    top: -4px;
    right: -4px;
    bottom: -4px;
    border: 1px solid #ff9500;
    border-radius: 39px;
    margin-bottom: 0px;
}

.main-button:focus{
    outline: none;
}

.main-button-notactive{
    opacity: 0.3;
    background-color: #ff9500;
    border: 1px solid #ff9500;
}

.bt-opacity{
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid white;
}

.bt-opacity span{
    display: none;
}

.with-word{
    background-color: #3d3d3d;
}

.with-word span{
    border-color: #3d3d3d;
}

@media (max-width: 576px){

    .main-button{
        width: 250px;
        min-height: 50px;
        font-size: 1rem;
        margin: 10px 0;
    }

    .main-button span{
        left: -3px;
        top: -3px;
        right: -3px;
        bottom: -3px;
        border-radius: 38px;
    }
}

