.main-input{
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid white;
    border-radius: 35px;
    width: 300px;
    min-height: 70px;
    font-size: 1.4rem;
    font-weight: lighter;
    margin: 20px 0;
    color: white;
    font-family: inherit;
    padding: 10px 0 10px 20px;
}

.main-input:focus{
    outline: none;
}

.main-input-warning{
    border-color: #d06f66
}

@media (max-width: 576px){
    .main-input{
        width: 250px;
        min-height: 50px;
        font-size: 1rem;
        margin: 10px 0;
    }
}